import {
  AfterViewInit,
  Component,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  SimpleChanges,
  ViewChild
} from '@angular/core';
import { gettext } from '@c8y/ngx-components';
import { IOperation, IOperationBulk, IResultList, OperationStatus } from '@c8y/client';
import { StatusFilterComponent } from '@c8y/ngx-components/operations/status-filter';
import { BulkOperationsService } from '@c8y/ngx-components/operations/bulk-operations-service';
import {
  StatusOption,
  OperationStatusOptionsMap,
  OPERATION_STATUS_LABELS,
  OPERATION_STATUS_OPTIONS_MAP
} from '@c8y/ngx-components/operations/shared';

@Component({
  selector: 'c8y-single-operations-list',
  templateUrl: './single-operations-list.component.html'
})
export class SingleOperationsListComponent implements AfterViewInit, OnChanges {
  @Input()
  bulkOperation: Partial<IOperationBulk>;
  @Input()
  readOnly: boolean = false;
  @Output() onRetryFailedOperations: EventEmitter<Partial<IOperationBulk>> = new EventEmitter();

  @ViewChild(StatusFilterComponent, { static: false })
  statusFilter: StatusFilterComponent;

  OPERATION_STATUS = { ...OperationStatus, ALL: gettext('ALL') };
  OPERATION_STATUS_LABELS = OPERATION_STATUS_LABELS;
  OPERATION_STATUS_OPTIONS_MAP: OperationStatusOptionsMap = OPERATION_STATUS_OPTIONS_MAP;
  singleOperations: IResultList<IOperation>;

  constructor(private bulkOperationsService: BulkOperationsService) {}

  async ngAfterViewInit() {
    if (this.statusFilter) {
      this.filterOperationsByType();
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.bulkOperation && !changes.bulkOperation.firstChange) {
      this.filterOperationsByType();
    }
  }

  filterOperationsByType() {
    if (this.statusFilter) {
      const typeFilter = this.getFilterTypeOfSingleOperations();
      this.statusFilter.preset(
        typeFilter === this.OPERATION_STATUS.ALL
          ? []
          : [{ label: this.OPERATION_STATUS_LABELS[typeFilter] }]
      );
    } else {
      setTimeout(this.filterOperationsByType.bind(this));
    }
  }

  getFilterTypeOfSingleOperations() {
    const progress = this.bulkOperation.progress;

    if (progress.failed > 0) {
      return this.OPERATION_STATUS.FAILED;
    } else if (progress.failed === 0 && progress.successful === 0 && progress.pending === 0) {
      return this.OPERATION_STATUS.EXECUTING;
    } else if (progress.failed === 0 && progress.successful === 0 && progress.executing === 0) {
      return this.OPERATION_STATUS.PENDING;
    } else if (progress.failed === 0 && progress.pending === 0 && progress.executing === 0) {
      return this.OPERATION_STATUS.SUCCESSFUL;
    }

    return this.OPERATION_STATUS.ALL;
  }

  async getOperationsByStatus(filter: StatusOption[]) {
    this.singleOperations = await this.bulkOperationsService.getSingleOperationsByStatus(
      filter[0] && filter[0].label,
      this.bulkOperation.id
    );
  }

  retryBulkOperation() {
    this.onRetryFailedOperations.emit(this.bulkOperation);
  }
}
