import { Component, ElementRef, Input, ViewChild } from '@angular/core';
import { InventoryService, IOperation, OperationService } from '@c8y/client';
import { AlertService, gettext } from '@c8y/ngx-components';
import {
  OperationStatusOptionsMap,
  OPERATION_STATUS_LABELS,
  OPERATION_STATUS_OPTIONS_MAP
} from '@c8y/ngx-components/operations/shared';
import { omit } from 'lodash-es';

@Component({
  selector: 'c8y-single-operation-item',
  templateUrl: './single-operation-item.component.html'
})
export class SingleOperationItemComponent {
  device = {};
  OPERATION_STATUS_LABELS = OPERATION_STATUS_LABELS;
  OPERATION_STATUS_OPTIONS_MAP: OperationStatusOptionsMap = OPERATION_STATUS_OPTIONS_MAP;
  @Input()
  operation: IOperation;
  @Input()
  readOnly: boolean = false;
  @ViewChild('failureReason', { static: true }) failureReasonRef: ElementRef;

  constructor(
    private alertService: AlertService,
    private inventoryService: InventoryService,
    private operationService: OperationService
  ) {}

  async ngOnInit() {
    this.device = (await this.inventoryService.detail(this.operation.deviceId))?.data;
  }

  async retrySingleOperation(singleOperation) {
    const operationToCreate = omit(singleOperation, [
      'id',
      'self',
      'status',
      'failureReason',
      'creationTime',
      'delivery'
    ]);

    try {
      await this.operationService.create(operationToCreate);
      this.alertService.success(gettext('Operation created.'));
    } catch (er) {
      this.alertService.addServerFailure(er);
    }
  }
}
